
body{
  background-color: #22223B;
  color: whitesmoke;
  height: 100%;
}

div{
  margin:10px;
}

* {
  font-family: 'Merriweather', serif;
}

h1, h2, h3, h4 {
  font-family: 'Montserrat', sans-serif;
}
header h1, header h2, header p, h2{
  display:flex;
  flex-direction: column;
  align-items: center;
  padding:10px;
  margin:0;
  color:whitesmoke;
}

header{
  background-color: #4A4E69;
  margin:0;
}

a {
  color: whitesmoke;
  text-decoration: none;
  margin-right: 10px;
  margin-bottom: 5px;
  border: whitesmoke solid;
  padding: 5px;
  border-radius: 16px;
}

img{
  max-width: 50%;
}

a:hover {
  background-color: #22223B;
}

div.row{
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
}
div.showPets{
  display:flex;
  flex-direction: column;
  align-items: center;

}